// 入口文件

//引入vue (npm i vue 啦) 缺少 模板解析器的版本 在 vue文件的package.json的"module"可以看到
import Vue from "vue";

//完整版vue
// import Vue from 'vue/dist/vue'
//配合下面使用
// new Vue({
//   template: "<h1>Vue</h1>",
// }).$mount('#app') //挂载上窗口啦 el:"#app" 等效

//引入App啦，二把手啦
import App from "./App.vue";

//引入插件
import addon from "./plugin";
Vue.use(addon, "Hello World");

//关才tip
Vue.config.productionTip = false;

//我自己想把这个对象挂到Vue上
import car from "pubsub-js";

import store from "./store/index";

// /**ele  这么引入文件大的吓人*/
// import ElementUI from 'element-ui';
// import 'element-ui/lib/theme-chalk/index.css';
// Vue.use(ElementUI)

import VueRouter from "vue-router";
Vue.use(VueRouter)
import router from "./router";

import {
  Button,
  Row,
  DatePicker,
  Divider,
  Card,
  Progress,
  Avatar,
  Col,
  Result,
} from "element-ui";
Vue.component(Button.name, Button);
Vue.component(Row.name, Row);
Vue.component(DatePicker.name, DatePicker);
Vue.component(Divider.name, Divider);
Vue.component(Card.name, Card);
Vue.component(Progress.name, Progress);
Vue.component(Avatar.name, Avatar);
Vue.component(Col.name, Col);
Vue.component(Result.name, Result);
//new Vue 实例啦
new Vue({
  store,
  render: (h) => h(App),
  //上面的东西大概就是下面的东西
  // render(createElement) {
  //   //还可以生成html标签createElement("h1","h1里的内容")
  //   return createElement(App)
  // },
  beforeCreate() {
    Vue.prototype.$bus = this; //安装公交车啦
    Vue.prototype.$car = car;
  },
  router,
}).$mount("#app"); //挂载上窗口啦 el:"#app" 等效

/*
 1 vue.js 与 vue.runtime.xxx.js的区别
 vue.js是完整的，包括核心功能(监听，事件之类的) 和 模板解析器
 runtime 是运行版，只有极昼功能，没有模板解析器
 esm 是 ES module 语法用的vue
 common 是 commonJs 语法用的vue

 因为没有模板解析器，所以不能在vm中使用template ，而是使用在render函数要接收createElement 函数 (h) 要"解析"模板
 但是在vc中，脚手加使用 "vue-template-compiler" 包来解析 <template>


 去掉 模板解析器，可以在webpack 打包时缩小体积
 */
