<template>
  <div>
    <button @click="api1">获取api1</button>
    <br /><br />
    <button @click="api2">获取api2</button>
  </div>
</template>

<script>
import axios from "axios";
export default {
  methods: {
    api1() {
      axios.get("http://localhost:8080/api/1111").then(
        (response) => {
          console.log("api1 OK", response);
        },
        (error) => {
          console.log("api1 Failed", error);
        }
      );
    },
    api2() {
      axios.get("http://localhost:8080/api/").then(
        (response) => {
          console.log("api2 OK", response);
        },
        (error) => {
          console.log("api2 Failed", error);
        }
      );
    },
  },
};
</script>

