<template>
  <transition
    name="animate__animated animate__bounce"
    enter-active-class="animate__backInLeft"
    leave-active-class="animate__backOutRight"
    appear
  >
    <li>
      <label>
        <!-- 下面的用法用来指定一个属性要还是不要 -->
        <!-- <input type="checkbox" :checked="item.done" /> -->
        <!-- <input type="checkbox" v-model="item.done" /> -->
        <!-- 说一个事，我忘了 Vue.set()的用法了，。。。 -->
        <input
          type="checkbox"
          :checked="item.done"
          @change="$bus.$emit('trigger', item.id)"
        />
        <span v-show="!editting">{{ item.item }}</span>
        <input
          v-show="editting"
          type="text"
          :value="item.item"
          @blur="changeName"
          @keyup.enter="changeName"
          ref="inputbox"
        />
      </label>
      <button class="btn btn-danger" @click="remove">删除</button>
      <button class="btn btn-edit" @click="edit" v-show="!editting">
        编辑
      </button>
      <button class="btn btn-edit" v-show="editting">保存</button>
    </li>
  </transition>
</template>

<script>
import "animate.css";
export default {
  name: "Item",
  //这里直接用了修改了props来的东西，不好
  props: ["item"],
  data() {
    return {
      editting: false,
    };
  },
  methods: {
    changeName(e) {
      this.editting = false;
      //   console.log("changeName", this.item.id, e.target.value);
      this.$bus.$emit("changeName", this.item.id, e.target.value);
    },
    remove() {
      //   this.show = false;
      this.$car.publish("remove", this.item.id);
    },
    edit() {
      this.editting = true;
      if (this.editting)
        this.$nextTick(() => {
          this.$refs.inputbox.focus();
        });
      //   or
      //   setTimeout(() => {
      //     this.$refs.inputbox.focus();
      //   });

      //   if (!this.editting)
      //     this.$$nextTick(() => {
      //       this.$refs.inputbox.focus();
      //     });
    },
  },
  mounted() {
    // console.log("@", this);
  },
};
</script>
<style lang="less" scoped>
li {
  list-style: none;
  height: 36px;
  line-height: 36px;
  padding: 0 5px;
  border-bottom: 0.5px solid #ddd;
  border-top: 0.5px solid #ddd;
}

li label {
  float: left;
  cursor: pointer;
}

li label li input {
  vertical-align: middle;
  margin-right: 6px;
  position: relative;
  top: -1px;
}

li:before {
  content: initial;
}

li:last-child {
  border-bottom: none;
}
.btn {
  margin-right: 5px;
}
li {
  .btn {
    float: right;
    display: none;
    margin-top: 3px;
  }
}
li:hover {
  background-color: silver;
  .btn {
    display: inline-block;
  }
}
</style>