<template>
  <div class="todo-footer" v-show="todos.length">
    <label>
      <input type="checkbox" v-model="isAll" />
    </label>
    <span>
      <span>已完成{{ done }}</span> /
      {{ todos.length }}
    </span>
    <button class="btn btn-danger" @click="$emit('clear')">
      清除已完成任务
    </button>
  </div>
</template>

<script>
export default {
  name: "Bottom",
  props: ["todos"],
  computed: {
    done() {
      //   return this.todos.filter((i) => i.done).length;
      return this.todos.reduce((per, todo) => per + (todo.done ? 1 : 0), 0);
    },
    isAll: {
      get() {
        return this.done === this.todos.length && this.todos.length != 0;
      },
      set(e) {
        this.$emit("doneAll", e);
      },
    },
  },
};
</script>
<style lang="less" scoped>
.todo-footer {
  text-align: left;
  height: 40px;
  line-height: 40px;
  padding-left: 6px;
  margin-top: 5px;
}

.todo-footer label {
  display: inline-block;
  margin-right: 20px;
  cursor: pointer;
}

.todo-footer label input {
  position: relative;
  top: -1px;
  vertical-align: middle;
  margin-right: 5px;
}

.todo-footer button {
  float: right;
  margin-top: 5px;
}
</style>