<template>
  <div>
    <p>{{ something }}</p>
    <h2>通过props实现</h2>
    <h2>自定义的说法是：给Event组件的实例对象绑定了一个xxx事件</h2>
    <p>
      这里还有一个问题，如果在组件上绑定原生事件，组件会把它当作自定义事件，要加一个.native修饰符
    </p>
    <Event
      :getSomething="getSomething"
      @getSomething="getSomething"
      ref="event"
      @click.native="click"
    ></Event>

    <!-- 一次性事件 -->
    <!-- <Event
      :getSomething="getSomething"
      @getSomething.once="getSomething"
      ref="event"
    ></Event> -->
    <hr />
  </div>
</template>

<script>
import Event from "./Event.vue";
export default {
  name: "EventApp",
  components: {
    Event,
  },
  data() {
    return {
      something: "",
    };
  },
  methods: {
    getSomething(param, ...arr) {
      console.log(param);
    },
    click(p) {
      console.log("click.native");
    },
  },
  mounted() {
    //通过vc实例来绑定事件,灵活，比如延时绑定事件
    // this.$refs.event.$on("someEvent", this.getSomething);
    this.$refs.event.$on("someEvent", (param) => {
      this.something = param;
    });
    // 就捕获一次
    // this.$refs.event.$once("someEvent", this.getSomething);
  },
};
</script>

