<template>
  <div>
    <div class="byVue">
      <h1>当前和为:{{ vue.sum }}</h1>

      <select v-model="vue.base">
        <option :value="1">1</option>
        <option :value="2">2</option>
        <option :value="3">3</option>
      </select>
      <button @click="vue.sum += vue.base">+</button>
      <button @click="vue.sum -= vue.base">-</button>
      <button @click="vue.sum % 2 != 0 ? (vue.sum += vue.base) : vue.sum">
        和为奇数可加
      </button>
      <button @click="add">等一等再加</button>
    </div>

    <hr />
    <div class="byVue">
      <h1>当前和为:{{ $store.state.x_sum }}</h1>
      <h1>(通过mapState)当前和为:{{ x_sum }}</h1>
      <h1>当前和*10为:{{ $store.getters.bigger }}</h1>
      <h1>当前和*10为:{{ bigger }}</h1>
      <select v-model="vue.base">
        <option :value="1">1</option>
        <option :value="2">2</option>
        <option :value="3">3</option>
      </select>
      <button @click="$store.commit('add', vue.base)">+</button>
      <button @click="$store.commit('add', -vue.base)">-</button>
      <button
        @click="
          $store.state.x_sum % 2 != 0 ? $store.commit('add', vue.base) : 0
        "
      >
        和为奇数可加
      </button>
      <button @click="add_x">等一等再加</button>
    </div>

    <hr />
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
export default {
  name: "CountApp",
  data() {
    return {
      vue: { base: 1, sum: 0 },
    };
  },
  methods: {
    add() {
      setTimeout(() => {
        this.vue.sum += this.vue.base;
      }, 500);
    },
    add_x() {
      this.$store.dispatch("add_x", this.vue.base);
    },
    // ...mapMutations(["add"]),//还有对象的写法，这里不重复了，重点是传参问题，他生成 add(p){ this.$store.commit("add",p) } 你明白了吧，我还不知道多个参数会怎么样
    // ...mapActions([]),
  },
  computed: {
    // ...mapState({ x_sum: "x_sum" }),//等效下面
    ...mapState(["x_sum"]),
    ...mapGetters(["bigger"]),
  },

  mounted() {},
};
</script>

<style lang="less" scoped>
.byVue {
  button {
    margin: 5px;
  }
}
</style>