<template>
  <div id="root">
    <div class="todo-container">
      <div class="todo-wrap">
        <InputBox @addTodo="add"></InputBox>
        <TodoList :todos="todos"></TodoList>
        <Bottom :todos="todos" @doneAll="doneAll" @clear="clear"></Bottom>
      </div>
    </div>
    <hr />
  </div>
</template>

<script>
import Bottom from "./Bottom.vue";
import InputBox from "./InputBox.vue";
import List from "./List.vue";
export default {
  name: "TodoListApp",
  components: {
    Bottom,
    InputBox,
    TodoList: List,
  },
  data() {
    return {
      todos: JSON.parse(localStorage.getItem("todos")) || [],
    };
  },
  watch: {
    todos: {
      deep: true,
      handler(newV, oldV) {
        localStorage.setItem("todos", JSON.stringify(newV));
      },
    },
  },
  methods: {
    add(todo) {
      this.todos.unshift(todo);
    },
    trigger(id) {
      this.todos.forEach((i) => {
        if (i.id === id) i.done = !i.done;
      });
    },
    remove(id) {
      if (confirm("delete?")) {
        this.todos = this.todos.filter((i) => i.id !== id);
      }
    },
    doneAll(done) {
      this.todos.forEach((todo) => {
        todo.done = done;
      });
    },
    clear() {
      this.todos = this.todos.filter((i) => !i.done);
    },
    updateName(id, name) {
      this.todos.forEach((i) => {
        if (i.id === id) i.item = name;
      });
    },
  },
  mounted() {
    this.$bus.$on("remove", this.remove);
    this.$bus.$on("trigger", this.trigger);
    this.$bus.$on("changeName", this.updateName);
    this.psid = this.$car.subscribe("remove", (_, id) => {
      this.todos = this.todos.filter((i) => i.id !== id);
    });
  },
  beforeDestroy() {
    this.$bus.$off(["remove", "trigger"]);
    this.$car.unsubscribe(this.psid);
  },
};
</script>
<style lang="less" scoped>
/deep/ .btn {
  display: inline-block;
  padding: 4px 12px;
  margin-bottom: 0;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2),
    0 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 4px;
}

/deep/ .btn-danger {
  color: #fff;
  background-color: #da4f49;
  border: 1px solid #bd362f;
}

/deep/ .btn-edit {
  color: #fff;
  background-color: #3fd131;
  border: 1px solid #3fd131;
}

/deep/ .btn-danger:hover {
  color: #fff;
  background-color: #bd362f;
}

/deep/ .btn:focus {
  outline: none;
}

/deep/ .todo-container {
  width: 600px;
  margin: 0 auto;
}
/deep/ .todo-container .todo-wrap {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}
</style>