<template>
  <div>
    <h3 class="title">这里写一个给组件加局部样式</h3>
    <p>
      如果不做限制，在vue把总有组件的样式汇总之后，同类名的会根据App.vue中的import顺序进行覆盖
    </p>
    <p>加上scoped之后，会在组件上加一个随机属性来区分样式</p>
    <p>再说一个，可以使用less，但在要安装 less-loader</p>
    <p>通过 <span> npm list webpack</span> 看安装的webpack版本</p>
    <p>通过 <span>npm view webpack versions</span> 看webpack全版本</p>
    <p>一般webpack 4 的话 安装 less 4 安装 less-loader 7</p>
    <p>npm i less</p>
    <p>npm i less-loader@7</p>
    <p class="alert">App.vue的style，一般还是不要加scoped</p>
    <hr />
  </div>
</template>

<script>
export default {
  name: "Style",
};
</script>
<style lang="less" scoped>
.alert {
  background-color: red;
}
p {
  span {
    color: royalblue;
  }
}
</style>