    <p ref="p2">
      应用在html标签上获取的是真实dom元素，应用在组件标签上获取组件实例对象
    </p>
    <button @click="getDomByRef">获取dom</button>
     <hr />
  </div>
</template>

<script>
export default {
  name: "Ref",
  methods: {
    getDomByRef() {
      console.log(this.$refs, this.$refs.title);
    },
  },
};
</script>

