<template>
  <div class="todo-header">
    <input
      type="text"
      placeholder="请输入你的任务名称，按回车键确认"
      v-model.trim="todo"
      @keyup.enter="add"
    />
  </div>
</template>

<script>
// npm i nanoid 一个生成唯一字符串的名，uuid的mini版
import { nanoid } from "nanoid";

//这里目前数据出不去，一种方法是在./App.vue给组件传一个方法进来，<VC :fn="fn"> 组件 props:["fn"]; fn(something) 通过方法把数据带出去，不过这里我不用，我不喜欢这种方式
export default {
  name: "InputBox",
  data() {
    return {
      todo: "",
    };
  },
  //   props: ["addTodo"],
  methods: {
    add(event) {
      if (!this.todo) return;
      //   this.addTodo({ id: nanoid(), item: this.todo, done: false });
      this.$emit("addTodo", { id: nanoid(), item: this.todo, done: false });
      this.todo = "";
    },
  },
};
</script>
<style lang="less" scoped>
.todo-header input {
  width: 560px;
  height: 28px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 4px 7px;
}

.todo-header input:focus {
  outline: none;
  border-color: rgba(82, 168, 236, 0.8);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 8px rgba(82, 168, 236, 0.6);
}
</style>