<template>
  <div>
    <div class="category">
      <Category title="电影">
        <ul slot="context">
          <li v-for="(i, index) in data" :key="index">{{ i }}</li>
        </ul>
      </Category>
      <Category title="动画">
        <ul slot="context">
          <li v-for="(i, index) in data" :key="index">{{ i }}</li>
        </ul>
      </Category>

      <ScopeSlot title="slot-scope">
        <!-- slot-scope支持解析赋值 -->
        <ol slot="footer" slot-scope="fromScope">
          <li v-for="(i, index) in fromScope.data" :key="index">{{ i }}</li>
          {{
            fromScope.somethind
          }}
        </ol>
      </ScopeSlot>

      <ScopeSlot title="slot-scope">
        <!-- slot-scope支持解析赋值 -->
        <h4 slot="footer" slot-scope="{ data, somethind }">
          {{ data }}-{{ somethind }}
        </h4>
        <template slot="footer" slot-scope="{ data }">
          <h4 v-for="(i, k) in data" :key="k">qwe</h4>
        </template>
      </ScopeSlot>
    </div>
    <hr />
  </div>
</template>

<script>
import Category from "./Category.vue";
import ScopeSlot from "./ScopeSlot.vue";
export default {
  components: {
    Category,
    ScopeSlot,
  },
  data() {
    return {
      data: ["123", "123", "123"],
    };
  },
};
</script>

<style lang="less" scoped>
.category {
  display: flex;
  justify-content: space-around;
}
</style>