<template>
  <div>
    <h3>{{ title }}</h3>
    <p>name: {{ name }}</p>
    <p>
      这里有说法，虽然也可以直接改props接收到的值，但是会有警告，这不好,可以理解为只读属性
    </p>
    <p>age:{{ age + 1 }}</p>
    <button @click="age++">直接改props接收到的值</button>
    <p>
      一种解决方法是，因为props接收早于data中的数据，所以可以data中使用this.xxx
    </p>
    <p>age:{{ myAge + 1 }}</p>
    <button @click="myAge++">把props放一份到data，并修改</button>
    <hr />
  </div>
</template>

<script>
export default {
  name: "Props",
  data() {
    return {
      title: "Props属性",
      myAge: this.age,
    };
  },
  //   props: ["name", "age"], //简单接收
  //   props: { name: String, age: Number },//接收时如果类型有问题，会有warn
  props: {
    //这个应该可以看明白吧
    name: { type: String, required: true },
    age: { type: Number, default: 99 },
  },
};
</script>

