<template>
  <div class="row">
    <div
      v-show="users.length"
      class="card"
      v-for="user in users"
      :key="user.id"
    >
      <a :href="user.html_url" target="_blank">
        <img :src="user.avatar_url" style="width: 100px" />
      </a>
      <p class="card-text">{{ user.login }}</p>
    </div>
    <h1 v-show="first">welcome!!</h1>
    <h1 v-show="status.loading">loading....</h1>
    <h1 v-show="status.error">{{ status.error }}</h1>
  </div>
</template>

<script>
export default {
  name: "Display",
  data() {
    return {
      first: true,
      status: {
        loading: false,
        error: "",
      },
      users: [],
    };
  },
  mounted() {
    this.$bus.$on("githubUsers", (data) => {
      this.first = false;
      this.status = { ...this.status, ...data.status };
      this.users = data.users;
    });
  },
};
</script>

<style lang="less" scoped>
.album {
  min-height: 50rem; /* Can be removed; just added for demo purposes */
  padding-top: 3rem;
  padding-bottom: 3rem;
  background-color: #f7f7f7;
}

.row {
  display: flex;
  flex-wrap: wrap;
  border: 1px dotted blue;
  justify-content: space-around;
}

.card {
  //   float: left;

  width: 15%;
  padding: 0.75rem;
  margin-bottom: 2rem;
  border: 1px solid #efefef;
  text-align: center;
  border: 1px dotted red;
}

.card > img {
  margin-bottom: 0.75rem;
  border-radius: 100px;
}

.card-text {
  font-size: 85%;
}
</style>
