<template>
  <div>
    <h3>自定义事件</h3>
    <p>这里为了让子组件给父组件传数组</p>
    <p>1.使用子调父方法的方式</p>
    <button @click="getSomething(data)">by methods</button>
    <br />
    <p>2.通过自定义事件实现</p>
    <button @click="$emit('getSomething', data)">emit event</button>
    <button @click="$emit('someEvent', data)">someEvent</button>
    <p>解绑</p>
    <button @click="$off('someEvent')">解绑someEvent</button>
    <button @click="$off(['getSomething', 'someEvent'])">解绑多个</button>
    <button @click="$off()">解绑自定义事件全部</button>
    <button @click="destroy">销毁当前对象</button>
  </div>
</template>

<script>
export default {
  name: "Event",
  props: {
    getSomething: {
      type: Function,
      default: function () {
        console.log(this.i);
      },
    },
  },
  methods: {
    destroy() {
      this.$emit("someEvent", "没了");
      this.$destroy();
      console.log("88");
    },
  },
  data() {
    return {
      data: "hi App",
      i: "hi myself",
    };
  },
};
</script>

