<template>
  <el-card :body-style="{ padding: '5px' }" shadow="hover" class="box-card">
    <el-row>
      <el-col :span="10" class="title cn">{{ title.cn }}</el-col>
      <el-col :span="14" class="title en">{{ title.en }}</el-col>
      <el-col :span="24"> <el-divider class="divider"></el-divider></el-col>
    </el-row>

    <template v-for="(i, index) in info">
      <el-row :key="index" :gutter="0">
        <el-col :span="8" class="key">{{ i.key }}</el-col>
        <el-col :span="1" class="delimiter">:</el-col>
        <el-col :span="15" class="value"
          ><el-progress
            :stroke-width="13"
            :text-inside="true"
            :percentage="i.value"
            :color="colors"
          ></el-progress
        ></el-col>
        <el-col :span="24"> <el-divider class="divider"></el-divider></el-col>
      </el-row>
    </template>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      percentage: 80,
      colors: [
        { color: "#fa6e6e", percentage: 5 },
        { color: "#ff7165", percentage: 10 },
        { color: "#ff765b", percentage: 15 },
        { color: "#ff7b50", percentage: 20 },
        { color: "#ff8144", percentage: 25 },
        { color: "#ff8837", percentage: 30 },
        { color: "#ff9028", percentage: 35 },
        { color: "#ff9812", percentage: 40 },
        { color: "#fea100", percentage: 45 },
        { color: "#f8aa00", percentage: 50 },
        { color: "#f0b300", percentage: 55 },
        { color: "#e6bc00", percentage: 60 },
        { color: "#dbc500", percentage: 65 },
        { color: "#cece00", percentage: 70 },
        { color: "#bfd700", percentage: 75 },
        { color: "#ade000", percentage: 80 },
        { color: "#98e800", percentage: 85 },
        { color: "#7ff000", percentage: 90 },
        { color: "#5cf800", percentage: 95 },
        { color: "#00ff00", percentage: 100 },
      ],
    };
  },
  props: ["title", "info"],
  methods: {},
};
</script>

<style lang="less" scoped>
.box-card {
  max-width: 300px;
  border: none;
  color: rgb(71, 71, 71);
  font-size: 14px;
  font-weight: 500;
  background-color: rgba(0, 0, 0, 0);
  white-space: nowrap;
  // text-overflow: ellipsis;
  // overflow: hidden;
  .title {
    color: rgb(15, 184, 206);
    font-size: 17px;
    &.en {
      padding-top: 2px;
      text-align: justify;
    }
    &.cn {
      padding-left: 8px;
    }
  }
  .key {
    padding: 0 0 0 34px;
  }

  .value {
    text-align: justify;
    padding: 0 0 0 5px;
  }
  .divider {
    margin: 4px 0;
    height: 0.5px;
    background-color: rgb(190, 190, 190);
  }
}
</style>