<template>
  <div>
    <p>
      就是vue给我们加了三个样式的类名，具体名字看style，就是发生过程中，到哪，从哪
    </p>
    <button @click="isShow = !isShow">显示/隐藏</button>
    <!-- <transition appear> 等效下面，就是加个appear属性 ，如果没有name属性，默认名字是v-->
    <transition name="animate" :appear="true">
      <h1 v-show="isShow">动画来了</h1>
    </transition>

    <button @click="isShow2 = !isShow2">显示/隐藏</button>
    <!-- <transition appear> 等效下面，就是加个appear属性 ，如果没有name属性，默认名字是v-->
    <transition name="transition1" :appear="true">
      <h1 v-show="isShow2">过渡来了</h1>
    </transition>

    <button @click="isShow3 = !isShow3">显示/隐藏</button>
    <!-- <transition appear> 等效下面，就是加个appear属性 ，如果没有name属性，默认名字是v-->
    <transition-group name="transition1" :appear="true">
      <h1 v-show="isShow3" v-for="i in 2" :key="i">过渡来了</h1>
    </transition-group>

    <p>第三方库</p>
    <button @click="isShow4 = !isShow4">显示/隐藏</button>
    <transition-group
      name="animate__animated animate__bounce"
      enter-active-class="animate__swing"
      leave-active-class="animate__backOutUp"
      appear
    >
      <h1 v-show="isShow4" key="1">第三方库</h1>
      <h1 v-show="!isShow4" key="2">过渡来了</h1>
    </transition-group>

    <hr />
  </div>
</template>

<script>
import "animate.css";
export default {
  data() {
    return {
      isShow: true,
      isShow2: true,
      isShow3: true,
      isShow4: true,
    };
  },
};
</script>

<style lang="less" scoped>

// 动画完成
h1 {
  background-color: skyblue;
}

.animate-enter-active {
  animation: qwe 1s;
}

.animate-leave-active {
  animation: qwe 1s reverse;
}

@keyframes qwe {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}

//过渡完成
.transition1-enter-active,
.transition1-leave-active {
  transition: 1s linear;
}
// 进入的起点 离开的终点
.transition1-enter,
.transition1-leave-to {
  transform: translateX(-100%);
}
//进入的终点 离开的起点
.transition1-enter-to,
.transition1-leave {
  transform: translateX(0);
}
// // 离开的起点
// .transition1-leave {
//   transform: translateX(0);
// }
// //离开的终点
// .transition1-leave-to {
//   transform: translateX(-100%);
// }
</style>