<template>
  <div>
    <p>收到的消息</p>
  </div>
</template>

<script>
import pubsub from "pubsub-js";
export default {
  mounted() {
    this.pubsubId = pubsub.subscribe("fromA", (msgName, data) => {
      console.log("@B", this, msgName, data);
    });
  },

  beforeDestroy() {
    pubsub.unsubscribe(this.pubsubId);
  },
};
</script>

