//先说下，vue插件是一个对象啦，要import的，所以先export
export default {
    //一定要有install,它接收到一个 Vue 的构造函数，我喜欢说是class
    //其他还接收其他,在Vue.use(xxx,1,2,3,4);
    install(Vue, saySomething) {
        //这里可用的Vue.filter()，Vue.prototype.xxx = ?? ； Vue.mixin() ；Vue.derective()
        Vue.prototype.saySomething = () => { console.log(saySomething); }
    }
}

